.btn {
  cursor: pointer;
  -webkit-user-select: none;
  user-select: none;
  text-align: center;
  text-transform: uppercase;
  animation: button-pop var(--animation-btn, .25s) ease-in-out;
  border: 1px;
  border-radius: 50px;
  outline: none;
  flex-wrap: wrap;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  height: 3rem;
  min-height: 3rem;
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: .875rem;
  font-weight: 600;
  line-height: 1em;
  text-decoration-line: none;
  transition: all .2s cubic-bezier(.4, 0, .2, 1);
