  background-color: #0de794;
  border: none;
  border-radius: 20px;
  padding: 10px 16px;
  font-size: 16px;
}

.dropdown {
  display: inline-block;
  position: relative;
}

.dropdown-content {
  z-index: 1;
  background-color: #f9f9f9;
  border-radius: 20px;
  min-width: 160px;
  display: none;
  position: absolute;
  box-shadow: 0 8px 16px #0003;
}

.dropdown-content a {
  color: #000;
